<template>
  <div>
    <!-- 面包屑导航区域 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/admin' }">首页</el-breadcrumb-item>
      <el-breadcrumb-item>模型训练</el-breadcrumb-item>
      <el-breadcrumb-item>模型训练1</el-breadcrumb-item>
    </el-breadcrumb>
    <!-- 卡片视图 -->
    <el-card>
      <el-table
          :data="tableData"
          border
          style="width: 100%">
        <el-table-column
            fixed
            prop="Mod_id"
            label="模型id"
            min_width="20%">
        </el-table-column>
        <el-table-column
            prop="Mod_address"
            label="模型地址"
            min_width="40%">
        </el-table-column>
        <el-table-column
            prop="Train_time"
            label="训练时间"
            min_width="40%">
        </el-table-column>
        <el-table-column
            prop="Acc"
            label="精确度"
            min_width="40">
        </el-table-column>
      </el-table>
      <div style="margin-top: 20px">
        <el-row type="flex" justify="start" style="border-top: 20px">
          <el-button @click="handleClick">开始训练</el-button>
          <el-button type="primary" @click="outerVisible = true">选择数据集</el-button>
          <el-dialog title="模型部署" :visible.sync="outerVisible">
            <el-card>
              <el-table
                  v-loading="loading"
                  ref="multipleTable"
                  :data="tableData"
                  style="width: 100%"
                  @selection-change="handleSelectionChange"
              >
                <el-table-column
                    type="selection"
                    min-width="55">
                </el-table-column>
                <el-table-column
                    fixed
                    prop="Mod_id"
                    label="模型id"
                    min_width="40"
                >
                </el-table-column>
                <el-table-column
                    prop="Mod_address"
                    label="模型地址"
                    min_width="20">
                </el-table-column>
                <el-table-column
                    prop="Train_time"
                    label="训练时间"
                    min_width="40">
                </el-table-column>
                <el-table-column
                    prop="Acc"
                    label="精确度"
                    min_width="40">
                </el-table-column>
                <el-table-column
                    label="操作"
                    min_width="40">
                  <template>
                    <el-button type="text" size="small" @click="'Hello', $event">选择</el-button>
                  </template>

                </el-table-column>
              </el-table>
            </el-card>
            <div style="float: left">{{ msg }}</div>
            <div style="margin-top: 20px;">
              <el-row type="flex" justify="start" style="border-top: 20px">
                <el-button @click="handleClick">开始训练</el-button>
                <el-button type="primary" @click="getSelected()">查看已选数据集</el-button>
                <el-button type="primary" :plain="true" @click="open">模型部署</el-button>
              </el-row>
            </div>
            <div slot="footer" class="dialog-footer">
              <el-button @click="outerVisible = false">取 消</el-button>
            </div>
            <el-dialog
                width="30%"
                title="内层 Dialog"
                :visible.sync="innerVisible"
                append-to-body>
            </el-dialog>
          </el-dialog>
        </el-row>
      </div>
      <div>

      </div>
    </el-card>
  </div>
</template>

<script>
import message from "element-ui/packages/message";

export default {
  methods: {
    //保存选中结果
    handleSelectionChange(val) {
      console.info(val);
      this.multipleSelection = val;
    },
    //获取选中结果
    getSelected() {
      console.info(this.multipleSelection);
      console.info(this.$refs.multipleTable.tableData);
      console.info(this.$refs.multipleTable);
    },
    open() {
      this.$message({
        message: '模型部署成功！',
        type: 'success'
      });
    },
  },

  data() {
    return {
      msg: "请选择模型：",
      value: new Date(),
      outerVisible: false,
      innerVisible: false,
      tableData: [{
        Mod_id: 'davince001',
        Mod_address: 'C:/model/model1',
        Train_time: '2023-09-08',
        Acc: '97%'
      },
        {
          Mod_id: 'davince002',
          Mod_address: 'C:/model/model2',
          Train_time: '2023-09-09',
          Acc: '80%'
        },
        {
          Mod_id: 'davince003',
          Mod_address: 'C:/model/model3',
          Train_time: '2023-09-10',
          Acc: '93%'
        },],
      multipleSelection: []
    }
  }
}
</script>